export default {
    name: 'Map',
    methods: {
        selectSpot(spot) {
            this.selectedSpot = spot;
            this.dialogOpen = true;
        },
        getSpots(line) {
            let spots = [];
            for (let spotId of line) {
                let dog = this.dogs[spotId] || this.emptySpot;
                spots.push({ spotId, dog });
            }
            return spots;
        },
        getGenderClass(dog) {
            if (!dog) {
                return '';
            }
            switch (dog.gender) {
                case "f":
                    return 'female text-h6';
                case "m":
                    return 'male text-h6';
                default:
                    return 'empty text-italic';
            }
        }
    },
    data() {
        return {
            mapData: [
                [16, 17, 18],
                [14, 15],
                [11, 12, 13],
                [9, 10],
                [6, 7, 8],
                [4, 5],
                [1, 2, 3],
            ],
            emptySpot: {
                name: 'עמדה ריקה',
                desc: 'מצאת כלב בעמדה הזאת? בבקשה ספר לאחד המתנדבים עם החולצות הכתומות'
            },
            selectedSpot: {},
            dialogOpen: false,

            dogs: {
                1: {
                    id: 1111,
                    name: 'רקסי',
                    gender: 'm',
                    age: 'שנה+',
                    desc: 'מקסים ושקט, מחונך לצרכים'
                },
                2: {
                    id: 2222,
                    name: 'שושה',
                    gender: 'f',
                    age: '7ח',
                    desc: 'שובבה ואנרגטית'
                },
                3: {
                    id: 3333,
                    name: 'מיילי',
                    gender: 'f',
                    age: '3ש',
                    desc: 'אוהבת טיולים וילדים'
                },
                4: {
                    id: 4444,
                    name: 'בוני',
                    gender: 'm',
                    age: '5ש',
                    desc: 'שקט ומחונך, אבל אוהב משחקים ושיגועים. מעולה לבית עם ילדים קטנים'
                },
                5: {
                    id: 5555,
                    name: 'רוקי',
                    gender: 'm',
                    age: '1-2 ש',
                    desc: 'הגיע מהסגר בערד'
                },

            }
        }
    },
    mounted() {
        setTimeout(() => {
            this.$el.querySelector('.entry').scrollIntoView({ behavior: "smooth", block: "end", inline: "nearest" });
        }, 1000);
    }

}
